export default {
    dsta(){
        return {
            myfollow2:''
        }
    },
    onShow(){
        this.getmyfollowlist()
    },
    methods: {
        getmyfollowlist(){
            this.$http.getmyfollow().then(res=>{
                if(res.status == 200) {
                    this.myfollow2 = res.data.data
                    console.log(this.myfollow2)
                }
            })
        }
    },
}
